<template>
  <div class="container">
    <h1 class="title">Register</h1>
    <b-form @submit.prevent="onRegister" @reset.prevent="onReset">
      <b-form-group
        id="input-group-username"
        label-cols-sm="3"
        label="Username:"
        label-for="username">
        <b-form-input
          id="username"
          v-model="$v.form.username.$model"
          type="text"
          placeholder="JohnDoe1"
          :state="validateState('username')">
        </b-form-input>
        <b-form-invalid-feedback v-if="!$v.form.username.required">
          Username is required
        </b-form-invalid-feedback>
        <b-form-invalid-feedback v-else-if="!$v.form.username.length">
          Username length should be between 3-8 characters long
        </b-form-invalid-feedback>
        <b-form-invalid-feedback v-if="!$v.form.username.alpha">
          Username alpha
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
        id="input-group-firstname"
        label-cols-sm="3"
        label="First Name:"
        label-for="firstname">
        <b-form-input
          id="firstname"
          type="text"
          v-model="form.firstName"
          placeholder="John">
        </b-form-input>
      </b-form-group>

      <b-form-group
        id="input-group-lastname"
        label-cols-sm="3"
        label="Last Name:"
        label-for="lastname">
        <b-form-input
          id="lastname"
          type="text"
          v-model="form.lastName"
          placeholder="Doe">
        </b-form-input>
      </b-form-group>

      <b-form-group
        id="input-group-country"
        label-cols-sm="3"
        label="Country:"
        label-for="country">
        <b-form-select id="country" v-model="form.selectedCountry" :options="form.countries" :state="validateState('selectedCountry')" ></b-form-select>
        <b-form-invalid-feedback v-if="!$v.form.selectedCountry.required">
          Country is required
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
        id="input-group-Password"
        label-cols-sm="3"
        label="Password:"
        label-for="password"
      >
        <b-form-input
          id="password"
          type="password"
          v-model="$v.form.password.$model"
          placeholder="abcd1234"
          :state="validateState('password')"
        ></b-form-input>
        <b-form-invalid-feedback v-if="!$v.form.password.required">
          Password is required
        </b-form-invalid-feedback>
        <b-form-text v-else-if="$v.form.password.$error" text-variant="info">
          Your password should be <strong>strong</strong>. <br />
          For that, your password should be also:
        </b-form-text>
        <b-form-invalid-feedback
          v-if="$v.form.password.required && !$v.form.password.length">
          Have length between 5-10 characters long
        </b-form-invalid-feedback>
        <b-form-invalid-feedback
          v-else-if="!$v.form.password.regexValid">
          Must contain at least one number, and one special character
        </b-form-invalid-feedback>

      </b-form-group>

      <b-form-group
        id="input-group-confirmedPassword"
        label-cols-sm="3"
        label="Confirm Password:"
        label-for="confirmedPassword"
      >
        <b-form-input
          id="confirmedPassword"
          type="password"
          v-model="$v.form.confirmedPassword.$model"
          :state="validateState('confirmedPassword')"
        ></b-form-input>
        <b-form-invalid-feedback v-if="!$v.form.confirmedPassword.required">
          Password confirmation is required
        </b-form-invalid-feedback>
        <b-form-invalid-feedback
          v-else-if="!$v.form.confirmedPassword.sameAsPassword"
        >
          The confirmed password is not equal to the original password
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
      id="Email"
      label-cols-sm="3"
      label="Email address:"
      label-for="EmailAddress"
      description="We'll never share your email with anyone else."
    >
      <b-form-input
        id="EmailAddress"
        v-model="$v.form.email.$model"
        type="text"
        :state="validateState('email')"
        placeholder="Enter email"
        required
      ></b-form-input>
      <b-form-invalid-feedback v-if="!$v.form.email.required">
        Email is required
      </b-form-invalid-feedback>
      <b-form-invalid-feedback v-else-if="!$v.form.email.regexEmail">
        Email Pattern Illegal
      </b-form-invalid-feedback>

    </b-form-group>
        <!-- <b-form-invalid-feedback v-if="!$v.form.email.validEmail">
          Invalid email format
        </b-form-invalid-feedback> -->


      <b-button type="reset" variant="danger">Reset</b-button>
      <b-button
        type="submit"
        variant="primary"
        style="width:250px;"
        class="ml-5 w-75"
        >Register</b-button
      >
      <div class="mt-2">
        You have an account already?
        <router-link to="login"> Log in here</router-link>
      </div>
    </b-form>
    <b-alert
      class="mt-2"
      v-if="form.submitError"
      variant="warning"
      dismissible
      show
    >
      Register failed: {{ form.submitError }}
    </b-alert>
    <!-- <b-card class="mt-3 md-3" header="Form Data Result">
      <pre class="m-0"><strong>form:</strong> {{ form }}</pre>
      <pre class="m-0"><strong>$v.form:</strong> {{ $v.form }}</pre>
    </b-card> -->
  </div>
</template>

<script>
import {countriess} from "../assets/consts.js";
import {
  required,
  minLength,
  maxLength,
  alpha,
  sameAs,
  email,} from "vuelidate/lib/validators";
export default {
  name: "Register",
  data() {
    return {
      form: {
        countries:countriess,
        selectedCountry: "",
        username: "",
        firstName: "",
        lastName: "",
        password: "",
        confirmedPassword: "",
        email: "",
        submitError: undefined
      },
      errors: [],
      // validated: false
    };
  },
  validations: {
    form: {
      username: {
        required,
        length: (u) => minLength(3)(u) && maxLength(8)(u),
        alpha
      },
      selectedCountry: {
        required
      },
      password: {
        required,
        regexValid: (value) => /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{5,10}$/.test(value),
        length: (p) => minLength(5)(p) && maxLength(10)(p)
      },
      confirmedPassword: {
        required,
        sameAsPassword: sameAs("password")
      },
      email: {
        required,
        regexEmail: (value) => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value)
      }
    }
  },
  mounted() {
  },
  methods: {
    validateState(param) {
      const { $dirty, $error } = this.$v.form[param];
      return $dirty ? !$error : null;
    },
    async Register() {
      try {
        const response = await this.axios.post(
          this.$root.store.store_state.server_domain + "auth/Register",
          {
            username: this.form.username,
            password: this.form.password,
            firstname: this.form.firstName,
            lastname: this.form.lastName,
            country: this.form.selectedCountry,
            email: this.form.email
          }
        );
        this.$router.push("/login");
        // console.log(response);
      } catch (err) {
        console.log(err.response);
        this.form.submitError = err.response.data.message;
      }
    },
    onRegister() {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }
      // console.log("register method go");
      this.Register();
    },
    onReset() {
      this.form = {
        username: "",
        firstName: "",
        lastName: "",
        selectedCountry: "",
        password: "",
        confirmedPassword: "",
        email: ""
      };
      this.$nextTick(() => {
        this.$v.$reset();
      });
    }
  },};
</script>
<style lang="scss" scoped>
.container {
  max-width: 500px;
}
</style>
